:root {
  --primary-color: #4E74F5;
  --second-color: #F90043;
  --gray-color: rgba(153, 153, 153, 60%);
  --link-color: #4E74F5;
  --primary-text-color: #000000;
  --second-text-color: #F90043;
  --error-text-color: #FB2424;
  --gray-text-color: rgba(153, 153, 153, 60%);

}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 14px;
}

body {
  padding: 1rem;
  margin: 0 auto;
  line-height: 1.5;
  font-size: 0.8rem;
  font-family: 'Open Sans', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  font-size: 1rem;
}

h1 {
  font-size: 2.9rem;
  font-weight: 600;
}

a {
  display: block;
  color: var(--link-color);
  font-size: 0.8rem;
  font-weight: 700;
  text-decoration: none;
}

form > input:last-of-type {
  margin-bottom: 0.3rem;
}

.errorBorder {
  border: 1px solid var(--error-text-color);
}

.errorBorder:focus,
.errorBorder:hover {
  border: 1px solid var(--error-text-color);
}

@media only screen and (min-width: 620px) {
  body {
    padding: 1rem 2rem;
  }

  a {
    font-size: 1rem;
  }

  h1 {
    font-size: 4rem;
  }

  .wrap {
    width: 500px;
    margin: 0 auto;
  }
}
