.input {
  padding: 1rem;
  margin-bottom: 1rem;
  width: 100%;
  background: transparent;
  border: 1px solid var(--gray-text-color);
  border-radius: 0.4rem;
  font-size: 1rem;
  font-weight: 600;
  transition: 0.3s all ease-in;
  outline: none;
}

.input:focus,
.input:hover {
  border: 1px solid var(--primary-color);
}
