.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.logo {
  color: var(--second-text-color);
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 600;
  text-transform: uppercase;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.desc {
  display: none;
}

.link {
  padding: 1rem 1.2rem;
  min-width: 8rem;
  text-align: center;
  border-radius: 100px;
  border: none;
  text-decoration: none;
  color: white;
  cursor: pointer;
  transition: 0.3s all ease-in;
  background-color: var(--primary-color);
}

.link:hover {
  background-color: #254dd1;
}

@media only screen and (min-width: 620px) {
  .desc {
    display: block;
    color: var(--gray-text-color);
    font-size: 1rem;
    margin-right: 1rem;
  }
}
