.button {
  padding: 1rem 1.2rem;
  border-radius: 2rem;
  border: none;
  color: white;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.3s all ease-in;
  background-color: var(--primary-color);
}

.button:hover {
  background-color: #254dd1;
}

.button:disabled {
  background-color: var(--gray-color);
  cursor: not-allowed;
}

@media only screen and (min-width: 620px) {
  .button {
    font-size: 1.1rem;
  }
}
