.card {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  width: 100%;
  border: 1px solid var(--gray-color);
  padding: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.cardLink {
  width: 100%;
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  color: var(--primary-text-color);
  text-decoration: none;
}

.cardIcon {
  max-width: 100%;
  width: 24px;
  height: 24px;
}

@media only screen and (min-width: 620px) {
  .card {
    width: 48%;
  }
}
