.main {
  padding: 2rem 0 1rem 0;
}

.titleWrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2.2rem;
  text-align: center;
}

.titleImg {
  max-width: 100%;
  width: 100px;
  height: 100px;
}

.desc {
  color: var(--gray-text-color);
  font-size: 1rem;
  margin-top: 0.4rem;
}

.img {
  max-width: 100%;
  width: 1rem;
  height: 1rem;
}

.gap {
  text-transform: uppercase;
  font-size: 0.9rem;
  color: var(--gray-text-color);
  text-align: center;
  margin-bottom: 1rem;
}

@media only screen and (min-width: 620px) {
  .desc {
    font-size: 1.1rem;
  }

  .img {
    font-size: 1.1rem;
  }

  .gap {
    font-size: 1.1rem;
  }
}