.card {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  width: 100%;
  border: 1px solid var(--gray-color);
  padding: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.cardLink {
  width: 100%;
  text-align: center;
  font-weight: 500;
  color: black;
  text-decoration: none;
}

.cardIcon {
  max-width: 100%;
  width: 2rem;
  height: 2rem;
}

.authenticationWrap {
  display: block;
}

@media only screen and (min-width: 620px) {
  .authenticationWrap {
    display: flex;
    justify-content: space-between;
  }

  .card {
    width: 48%;
  }
}
